#nav-menu {
  top: 0;
  width: 100%;
  position: fixed;
  text-decoration: "none";
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 5px 0px, rgba(0, 0, 0, 0.1) 0px 0px 1px 0px;
}
#nav-menu-links > *:hover {
  color: #2874f0;
  text-underline-offset: 10px;
  text-decoration-thickness: 3px;
  text-decoration-line: underline;
  text-decoration-color: #2874f0;
}



#home {
  /* border: 1px solid lightgrey; */
}
#home > div {
  /* border: 1px solid lightgrey; */
}
#home > div > img {
  border-radius: 50%;
  /* border: 1px solid lightgrey; */
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 5px 0px, rgba(0, 0, 0, 0.1) 0px 0px 1px 0px;
}


#about {
  text-align: center;
  /* border: 1px solid lightgrey; */
}


#skills {
  text-align: center;
  /* border: 1px solid lightgrey; */
}
#skills-main {
  width: 90%;
  margin: auto;
}
.skills-card {
  display: flex;
  padding: 4px 7px;
  align-items: center;
  border-radius: 4px;
  /* border: 1px solid lightgrey; */
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
.skills-card > img {
  height: 45px;
  margin-right: 10px;
}



.react-activity-calendar {
  margin: auto;
  padding: 20px 25px;
  border-radius: 4px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
#github-stats-main img {
  margin: auto;
  margin-top: 50px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

@media screen and (max-width: 1080px) {
    #github-stats-two {
      flex-direction: column;
    }
}



#contact-main {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
#contact-main > a > div {
  display: flex;
  padding: 7px 25px;
  align-items: center;
  flex-direction: column;
  /* border: 1px solid lightgrey; */
}

@media screen and (max-width: 410px) {
  #contact-main > a > div {
    margin-bottom: 25px;
  }
}